*, :before, :after {
  box-sizing: border-box;
  gap: 1rem;
  margin: 0;
  padding: 0;
}

body {
  flex-direction: column;
  align-items: center;
  margin: 1rem;
  display: flex;
}

header, #game-container, #result-display {
  flex-direction: column;
  align-items: center;
  display: flex;
}

#game-container {
  width: 100%;
  max-width: 600px;
  min-height: 500px;
}

#game-stats {
  flex-direction: row;
  display: flex;
}

.stats {
  padding: 1rem;
}

#computer-stats {
  text-align: right;
}

.hidden {
  display: none !important;
}

.visually-hidden {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.bar-wrapper {
  border: 1px solid #000;
  width: 100px;
  height: 20px;
  position: relative;
  overflow: hidden;
}

.bar {
  background-color: green;
  height: 100%;
  transition: width .3s;
  display: block;
  position: relative;
  overflow: hidden;
}

.bar-text {
  pointer-events: none;
  white-space: nowrap;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bar.full {
  background-color: #4caf50;
  width: 100%;
}

.bar.half {
  background-color: #ff9800;
  width: 50%;
}

.bar.zero {
  background-color: #f44336;
  width: 5%;
}

#choices {
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  gap: .5rem;
  width: 100%;
  display: flex;
}

.card-container {
  aspect-ratio: 150 / 210;
  cursor: pointer;
  text-align: center;
  background-color: #f9f9f9;
  border: 1px solid #0000001a;
  border-radius: 8px;
  flex-direction: column;
  flex: 1 1 0;
  justify-content: center;
  align-items: center;
  gap: .25rem;
  min-width: 80px;
  max-width: 150px;
  transition: transform .2s, box-shadow .2s, border-color .2s;
  display: flex;
  position: relative;
  box-shadow: 0 2px 6px #0000000d;
}

.card-container:not(:disabled):hover {
  border-color: #0003;
  transform: translateY(-5px)scale(1.03);
  box-shadow: 0 8px 20px #00000026;
}

.icon {
  font-size: 3rem;
  line-height: 1;
}

.label {
  color: #777;
  font-size: .8rem;
}

.card-container:disabled {
  opacity: .5;
  cursor: not-allowed;
  box-shadow: none;
  transform: none !important;
}
